import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable, of } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class AuthService {

  // private host: string;
  // private url: string;
  // private module = '/users';

  constructor(private http: HttpClient
              // private messageService: MessageService) {
  ) {

  }

  redirectUrl: string;

  isLoggedIn() {
    if (localStorage.getItem('currentUser')) {
      return true;
    }
    return false;
  }

  getAuthorizationToken() {
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    return currentUser ? currentUser.token : null;
  }

  getAppSubscriptionId() {
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    return currentUser.appSubscriptionId;
  }

  logout() {
    localStorage.removeItem('currentUser');
  }

  getHttpOptions = () => {

    const token = this.getAuthorizationToken();

    const headers = {'Content-Type': 'application/json; charset=UTF-8'};

    if (token) {
      headers['X-Access-Token'] = token;
    }

    return {
      headers: new HttpHeaders(headers)
    };
  }


  setItem(settings: any) {
    localStorage.setItem(settings.key, JSON.stringify(settings.object));
  }

  getItem(key: any) {
    const object = JSON.parse(localStorage.getItem(key));
    return object ? object : null;
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (result: any): Observable<T> => {

      // if (error && !error.auth) {
      //
      //   this.authService.logout();
      // }

      let message = result.message;

      if (result.error && result.error.msg) {
        message += ` ${result.error.msg}`;
      }

      console.log(message);
      // this.previewDialog.open({message: message});

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
