import {Injectable} from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import {catchError, finalize, map, take} from 'rxjs/operators';
import { v4 as uuidv4 } from 'uuid';

import { environment } from '../../environments/environment';
// import {ConfigService} from './config.service';
import {AuthService} from './security/auth.service';
import {FilePreviewOverlayService} from './file-preview-overlay.service';
import {AsyncValidatorFn, ValidationErrors} from '@angular/forms';

// const configService: ConfigService = new ConfigService();


const routes = [ 'api', 'globalize', 'socket', 'image', 'export', 'file', 'pdf' ];

let environmentUrl;

const buildUrl = (url: string) => {

  const route = url.split('/')[0];

  if (routes.indexOf(route) < 0) {
    url = 'api/' + url;
  }

  url = environment.api_url + '/' + url;

  // url = process.env.api_url;
  return url;
};

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  // private readonly host: string;
  // private url: string;
  // private module = '/companies';

  constructor(private http: HttpClient,
              private authService: AuthService,
              private previewDialog: FilePreviewOverlayService) {

    environmentUrl = environment.api_url;
  }

  public messagesErrors = {
    required: 'Required',
    email: 'Invalid email',
    taken: 'Already exists',
    forbidden: 'Nmero Prohibido'
  };

  getUrl() {
    return environmentUrl
  }

   /** GET Data from the server */
  get(url: string, settings?: any): Observable<any[]> {

    settings = settings || {};

    let previewDialogRef: any = null;

    if (settings.showPreviewDialog) {
      previewDialogRef = this.previewDialog.open();
    }

    if (!settings.options) {
      settings.options = {where: {appSubscriptionId: this.authService.getAppSubscriptionId()}};
    }
    else if (settings.options && !settings.options.where) {
      settings.options.where = {appSubscriptionId: this.authService.getAppSubscriptionId()};
    }
    else if (settings.options && settings.options.where && !settings.options.where.appSubscriptionId) {
      settings.options.where.appSubscriptionId = this.authService.getAppSubscriptionId();
    }

    return this.http.post<any[]>(buildUrl(url), settings, this.authService.getHttpOptions()).pipe(
      catchError(this.handleError<any[]>('get ' + url)),
      finalize(() => { if (previewDialogRef)  {previewDialogRef.close(); } })
    );
  }

  /** GET Data from the server and map in array of {value: 'id', text: 'field'} */
  getSelect(url: string, settings?: any): Observable<any[]> {

    const previewDialogRef = this.previewDialog.open();

    settings = settings || {};

    if (!settings.options) {
      settings.options = {where: {appSubscriptionId: this.authService.getAppSubscriptionId()}};
    }
    else if (settings.options && !settings.options.where) {
      settings.options.where = {appSubscriptionId: this.authService.getAppSubscriptionId()};
    }
    else if (settings.options && settings.options.where && !settings.options.where.appSubscriptionId) {
      settings.options.where.appSubscriptionId = this.authService.getAppSubscriptionId();
    }

    return this.http.post<any[]>(buildUrl(url), settings, this.authService.getHttpOptions()).pipe(
      map(value => {
        return value.map(value => { return {value: value[settings.value], text: value[settings.text]}})
      }),
      catchError(this.handleError<any[]>('getSelect ' + url)),
      finalize(() => { previewDialogRef.close() })
    );
  }

  /** GET hero by id. Return `undefined` when id not found */
  getEntity<Data>(url, id: any, settings?: any): Observable<any> {

    let previewDialogRef: any;

    if (settings && settings.previewDialog == false) {
      // const previewDialogRef = this.previewDialog.open();
    }
    else {
      previewDialogRef = this.previewDialog.open();
    }

    if (settings) {
      settings.id = id;
    }
    else {
      settings = {
        id
      };
    }

    return this.http.post<any>(buildUrl(url), settings, this.authService.getHttpOptions()).pipe(
      catchError(this.handleError<any>(`getEntity id=${id}`)),
      finalize(() => { if (previewDialogRef) {previewDialogRef.close()} })
    );
  }

  /** POST: add a new hero to the server */
  save(url, entity: any): Observable<any> {

    const previewDialogRef = this.previewDialog.open();

    let addId = (entity: any) => {

      if (Array.isArray(entity)) {

        for (let variable of entity) {
          addId(variable);
        }
      }
      else {

        for (let variable in entity) {

          if (Array.isArray(entity[variable])) {
            addId(entity[variable]);
          }
        }

        entity.id = entity.id || uuidv4();
        entity.appSubscriptionId = this.authService.getAppSubscriptionId();
      }
    }

    addId(entity);

    return this.http.put<any>(buildUrl(url), entity, this.authService.getHttpOptions()).pipe(
      catchError(this.handleError('add')),
      finalize(() => { previewDialogRef.close() })
    );
  }

  /** DELETE: delete the hero from the server */
  delete(url, entity: any | number): Observable<any> {

    const previewDialogRef = this.previewDialog.open();

    const id = typeof entity === 'string' ? entity : entity.id;
    const _url = `${buildUrl(url)}/${id}`;

    return this.http.delete<any>(_url, this.authService.getHttpOptions()).pipe(
      catchError(this.handleError<any>('delete')),
      finalize(() => { previewDialogRef.close() })
    );
  }

  /** DELETE: delete the hero from the server */
  deleteOptions(url, settings?: any): Observable<any> {

    const previewDialogRef = this.previewDialog.open();

    // const id = typeof entity === 'string' ? entity : entity.id;
    // const _url = `${buildUrl(url)}/${id}`;

    settings = settings || {};

    if (!settings.options) {
      settings.options = {where: {appSubscriptionId: this.authService.getAppSubscriptionId()}};
    }
    else if (settings.options && !settings.options.where) {
      settings.options.where = {appSubscriptionId: this.authService.getAppSubscriptionId()};
    }
    else if (settings.options && settings.options.where && !settings.options.where.appSubscriptionId) {
      settings.options.where.appSubscriptionId = this.authService.getAppSubscriptionId();
    }

    // let options: any = {};
    //
    // options = this.authService.getHttpOptions();
    // options.settings = settings

    // const _url = `${buildUrl(url)}/${settings}`;

    return this.http.post<any[]>(buildUrl(url) + '/delete', settings, this.authService.getHttpOptions()).pipe(
    // return this.http.delete<any>(_url, this.authService.getHttpOptions()).pipe(
      catchError(this.handleError<any>('deleteOptions')),
      finalize(() => { previewDialogRef.close() })
    );
  }

  takenValidator(url: string, settings?: any): AsyncValidatorFn {
    return (control: any): Observable<{ [key: string]: any } | null> => {

      settings = settings || {};

      const text = `${control.value}`.toLowerCase();
      let validationError: ValidationErrors = {'taken': true};

      if (settings.ValidationError == 'forbidden') {
        validationError = {'forbidden': true};
      }

      settings.showPreviewDialog = false;

      return this.get(url, settings).pipe(
        take(1),
        map((data: any) => data.map((o:any) => o[settings.field].toLowerCase()).indexOf(text) === -1),
        map(valid => valid ? null : validationError)
      )
    };
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (res: any): Observable<T> => {

      // if (error && !error.auth) {
      //
      //   this.authService.logout();
      // }

      let message = res.message;

      if (res.error && res.error.msg) {
        message += ` ${res.error.msg}`;
      }

      if (res.error && res.error.message) {
        message += ` ${res.error.message}`;
      }

      message += ` ${res.error.message}`;

      this.previewDialog.open({message});

      // Let the app keep running by returning an empty result.
      return of(res as T);
    };
  }
}
