import { Component, Input, Inject } from '@angular/core';

import { FilePreviewOverlayRef } from './file-preview-overlay-ref';
import { FILE_PREVIEW_DIALOG_DATA } from './file-preview-overlay.tokens';

@Component({
  selector: 'file-preview-overlay',
  template: `
    <div class="overlay-content">
      <img *ngIf="data.image" class="avatar" [src]="data.image.url">
      <p *ngIf="data.message" style="color: #f44336;">{{data.message}}</p>
    </div>
  `,
  styles: [`
    :host {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    h1 {
      margin: 0;
      padding: 1em;
    }

    img {
      width: 100%;
      max-width: 500px;
      height: auto;
    }

    .overlay-content {
      padding: 1em;
    }

    .avatar {
      /* cambia estos dos valores para definir el tamao de tu crculo */
      height: 300px;
      width: 300px;
      /* los siguientes valores son independientes del tamao del crculo */
      background-repeat: no-repeat;
      background-position: 50%;
      border-radius: 50%;
      background-size: 100% auto;
    }
  `]
})
export class FilePreviewOverlayComponent {

  constructor(
    public dialogRef: FilePreviewOverlayRef,
    @Inject(FILE_PREVIEW_DIALOG_DATA) public data: any) { }
}
